/* Sticky footer styles
-------------------------------------------------- */

html {
  position: relative;
  min-height: 100%;
}

body {
  /* Margin bottom by footer height */
  margin-bottom: 60px;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: 60px;
  line-height: 60px;
  /* Vertically center the text there */
  background-color: #f5f5f5;
}

/* Stylish, but not for sticky footer technique */

.footer > .container {
  padding-right: 15px;
  padding-left: 15px;
}

/* applied to all containers, it's absolutely needed
   for the first one (to avoid overlapping with navbar) */

body > .container {
  padding: 60px 15px 0;
}